import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import router from '../router/index'
Vue.use(Vuex)
const url = '/api'
// const url1 = 'http://localhost:3000/api'

export default new Vuex.Store({
    state: {
        token: '',
        mess_client: '',
        mess_login: '',
        mess_booklet: '',
        mess_deposit: '',
        mess_send: '',
        mess_search: '',
        mess_cus: '',
        sum: '',
        mess_staff: '',
        err: '',
        perBooklet:''
        
     
    },
    getters: {

    },
    actions: {
        
        perBooklet({commit,state}){
            axios.get('/api/booklet/perBooklet', {
                headers: { Authorization: `Bearer ${state.token}` },
              }).then(res =>commit('PERBOOKLET',res.data) )
        },
        registerCustomer({ commit }, data) {

            console.log(data)
            // axios.post(`${url}/customer/login`, data)
            axios.post(`${url}/customer/login`, data)
                .then(res => commit('TOKEN', res.data))
                // .then(res => commit('TOKEN', res.data))

                .catch(err => commit('ERR', err))
            // .then(res => console.log(res.data))


        },

        createCustomer({ commit }, data) {
            axios.post(`${url}/customer/create`, data)
                .then(res => commit('CREATE_USER', res.data))
        },
        createStaff({ commit }, data) {
            axios.post(`${url}/staff/create`, data)
                .then(res => commit('CREATE_USER', res.data))
        },
        createBooklet({ commit, state }, data) {

            // console.log(state.token)

            axios.post(`${url}/booklet/create`, data, { headers: { "Authorization": `Bearer ${state.token}` } })
                .then(res => commit('CREATE_BOOKLET', res.data))
        },
        depositMoney({ commit, state }, data) {

            axios.post(`${url}/booklet/deposit`, data, { headers: { "Authorization": `Bearer ${state.token}` } })
                .then(res => commit('CREATE_DEPOSIT', res.data))

        },
        loginStaff({ commit }, data) {
            // console.log('haaaa')
            axios.post(`${url}/staff/login`, data)

                .then(res => commit('TOKENSTAFF', res.data))
                .catch(err => commit('ERR', err))

        },
        sendMoney({ commit, state }, data) {



            axios.post(`${url}/booklet/send`, data, { headers: { "Authorization": `Bearer ${state.token}` } })
                .then(res => commit('CREATE_SEND', res.data))

        },
        getSum({ commit, state }) {

            axios.get(`${url}/booklet/sum`, { headers: { "Authorization": `Bearer ${state.token}` } })
                .then(res => commit('SUM', res.data))
                .catch(err => commit('ERR', err))

        },
        // searchMoney({commit,state},data){
        //     console.log('chan1')
        //     // http://localhost:3000/api/booklet/search
        //     axios.get(`http://localhost:3000/api/booklet/search`,data,{ headers: { "Authorization": `Bearer ${state.token}` } })
        //     .then(res => commit('CREATE_SEARCH', res.data))
        //     // .then(res => console.log(res))

        // },
        searchMoney({ commit, state }) {

            // console.log('tstt12')

            axios.get(`${url}/booklet/channn`, { headers: { "Authorization": `Bearer ${state.token}` } })
                .then(res => commit('CREATE_SEARCH', res.data))

        },

    },
    mutations: {
        TOKEN(state, data) {

            if (data.accessToken == null) {
                state.mess_login = 'sai mat khau'
            } else {
                state.token = data.accessToken
                state.mess_login = data.sign

                localStorage.setItem('accessToken', JSON.stringify(data.accessToken))
                router.push({ name: 'home' })
            }


        },
        // TOKEN(state, data) {

        //     console.log(data.accessToken)
        //     state.token = data.accessToken
        //     state.mess_login = data.sign
        //     localStorage.setItem('accessToken', JSON.stringify(data.accessToken))



        // },
        TOKENSTAFF(state, data) {

            if (data.accessToken == null) {
                state.err = 'sai mat khau'
            } else {
                state.token = data.accessToken
                state.mess_staff = data.sign
                localStorage.setItem('accessToken', JSON.stringify(data.accessToken))
            }

        },
        CREATE_USER(state, data) {
            state.mess_client = data.sign
        },
        activeTOKEN(state) {
            state.token = JSON.parse(localStorage.getItem('accessToken'))
        },
        CREATE_BOOKLET(state, data) {
            state.mess_booklet = data.sign
        },
        CREATE_DEPOSIT(state, data) {
            state.mess_deposit = data.sign

        },
        CREATE_SEND(state, data) {
            state.mess_send = data.sign

        },
        CREATE_SEARCH(state, data) {
            state.mess_search = data
            state.mess_cus = data.customer_id
        },
        SUM(state, data) {
            state.sum = data

        },
        ERR(state, data) {
            state.err = data
        },
        LOGOUT(state) {
            // console.log('tttt')
            state.token = ''
            localStorage.removeItem("accessToken");
            alert("ban da dang xuat");

        },
        PERBOOKLET(state,data){
            state.perBooklet =data
        }
        //test


    }
})