import Vue from 'vue'
import Router from 'vue-router'
import Register from '../components/Register'
import Login from '../components/Login'
import Home from '../components/Home'
import Booklet from '../components/Booklet'
import Deposit from '../components/Deposit'
import Send from '../components/Send'
import Search from '../components/Search'
import Staff from '../components/Staff'
import OnlyStaff from '../components/OnlyStaff'
import store from '../store'
Vue.use(Router)

export default new Router({
    // mode:'history',
    routes: [
        {
            path: '/register',
            name: 'register',
            component: Register
        },
        {
            path: '/staff',
            name: 'staff',
            component: Staff
        },
        {
            path: '/onlystaff',
            name: 'onlystaff',
            component: OnlyStaff,
            beforeEnter:(to,from,next)=>{
                // let Role = store.state.token
                // console.log(store.state.token.role)
                if(store.state.token){

                    next()
                }else{
                    alert("ban can dang nhap truoc");

                     next(false)
                }
            }
        },
        {
            path: '/booklet',
            name: 'booklet',
            component: Booklet,
            beforeEnter:(to,from,next)=>{
                // console.log(store.state.token.role)
                if(!store.state.token){
                    alert("ban can dang nhap truoc");
                    next(false);
                }else{
                     next()
                }
            }
        },
        {
            path: '/login',
            name: 'login',
            component: Login
        },
        {
            path: '/',
            name: 'home',
            component: Home
        }, 
        {
            path: '/deposit',
            name: 'deposit',
            component: Deposit,
            beforeEnter:(to,from,next)=>{

                // console.log(store.state.token.role)
                if(!store.state.token){
                alert("ban can dang nhap truoc");

                    next(false)
                }else{
                     next()
                }
            }
        }, 
        {
            path: '/send',
            name: 'send',
            component: Send,
            beforeEnter:(to,from,next)=>{

                // console.log(store.state.token.role)
                if(!store.state.token){
                alert("ban can dang nhap truoc");
                next(false)
                }else{
                     next()
                }
            }
        },
        {
            path: '/search',
            name: 'search',
            component: Search,
            beforeEnter:(to,from,next)=>{

                // console.log(store.state.token.role)
                if(!store.state.token){
                alert("ban can dang nhap truoc");
                next(false)
                }else{
                     next()
                }
            }
        }, 

    ]
})


